import {Button, useViewportGrid, Icon} from "@ohif/ui";
import React, {useEffect, useState} from "react";
import classNames from "classnames";
import PropTypes from "prop-types";
import {saveAs, FileSaver} from 'file-saver'
import JSZip from "jszip";
import ProgressBar from "@ramonak/react-progress-bar";
import { segmentation as cstSegmentation } from '@cornerstonejs/tools';
import { cache } from '@cornerstonejs/core';


function SegmentationImportExport({segmentationService, segmentations, cornerstoneViewportService}) : React.JSX.Element {

    const [isProcessingLayer, setIsProcessingLayer] = useState(false)
    const [processingLayerPercent, setProcessingLayerPercent] = useState(0)
    const [isMinimized, setIsMinimized] = useState(false);
    const [{ activeViewportIndex, viewports }, viewportGridService] = useViewportGrid();

    useEffect(() => {
        if(isProcessingLayer){
            setProcessingLayerPercent(0)
            setTimeout(exportSegmentation, 500)
        }
    }, [isProcessingLayer])

    const exportSegmentation = () => {
        const labelmapVolume = segmentationService.getLabelmapVolume(segmentations[0].id)
        const {
            dimensions,
            direction,
            scalarData
        } = labelmapVolume
        const segments_info = segmentations[0].segments
        const existing_segment_indexes = segments_info
            .map((s, i) => {if (s) return i})
            .filter(i => i)
        const data = new Uint32Array(scalarData)
        console.log('START COMPUTING INDEX ARRAY...')

        //Convert segmentation in PNG Images using JSON
        //Iterate through one layer (one PNG image)
        const all_layers_blobs = new Array<Blob>(dimensions[2] - 1)
        for(let layer_i = 0; layer_i < data.length; layer_i += (dimensions[0] * dimensions[1])){
            const current_processed_layer = layer_i / (dimensions[0] * dimensions[1])
            const segmented_layer = new Uint8ClampedArray(dimensions[0] * dimensions[1] * 4)
                .fill(0)
                .map((el, i) => {return (i - 3) % 4 === 0 ? 255 : el})
            const scalar_layer = data.slice(layer_i, layer_i + (dimensions[0] * dimensions[1]))
            //Map through existing segment indexes
            Promise.allSettled(existing_segment_indexes.map(segment_index => {
                //Get indexes of values that matches the segment_index in parameter
                const mapIndexSerial = (segment_index) => {
                    return scalar_layer
                        .map((v, i) => {if(v !== 0 && v === segment_index) return i})
                        .filter(i => i !== 0)
                }
                return mapIndexSerial(segment_index)
            })).then((indexes_array) => {
                //List of arrays of indexes matching a segment index (length = number of segment indexes)

                indexes_array.forEach((promise, i) => {
                    const segment_array = promise.value
                    //Fill segmented layer with corresponding segment color
                    if(segment_array.length !== 0){
                        const corresponding_segment_index = existing_segment_indexes[i]
                        const color_to_fill = [...segments_info[corresponding_segment_index].color, 255]
                        segment_array.forEach(index_to_color => {
                            segmented_layer[index_to_color * 4] = color_to_fill[0]
                            segmented_layer[index_to_color * 4 + 1] = color_to_fill[1]
                            segmented_layer[index_to_color * 4 + 2] = color_to_fill[2]
                        })
                    }
                })

                // console.log(`SEGMENTED ${current_processed_layer}`, segmented_layer)

                const fillingPromise = () => {
                    return new Promise<void>((resolve, reject) => {
                        const canvas = document.createElement('canvas')
                        const ctx = canvas.getContext('2d')
                        canvas.width = dimensions[0]
                        canvas.height = dimensions[1]
                        const imgData = new ImageData(segmented_layer, dimensions[0], dimensions[1], {colorSpace: "srgb"});
                        ctx.putImageData(imgData, 0, 0)
                        canvas.toBlob((blob) => {
                            if(blob){
                                all_layers_blobs[current_processed_layer] = blob
                                resolve()
                            }
                        }, "image/png")
                    })
                }
                fillingPromise().then(() => {
                    const completed_layers = all_layers_blobs.filter(el => {return el}).length
                    setProcessingLayerPercent(~~((completed_layers * 100) / dimensions[2]))
                })
            })
        }

        const waitUntilArrayDone = () => {
            return new Promise<void>((resolve, reject) => {
                (function waitForArray(){
                    const completed_layers = all_layers_blobs.filter(el => {return el}).length
                    if(completed_layers === dimensions[2]) return resolve();
                    else {
                        setTimeout(waitForArray, 500);}
                })()
            })
        }
        waitUntilArrayDone().then(() => {
            const zip = new JSZip()
            //Add layers in zip as PNG files
            all_layers_blobs.forEach((blob, index) => {
                zip.file(`layer_${index}.png`, blob)
            })

            //Add segments information as JSON File
            const json = JSON.stringify(segmentations[0].segments, null, '\t')
            const json_blob = new Blob([json], {type: "text/plain;charset=utf-8"});
            zip.file('segmentation_informations.json', json_blob)

            //Add scalar data as binary file
            const labelmapVolume = segmentationService.getLabelmapVolume(segmentations[0].id);
            const scalarData = new Uint8Array(labelmapVolume.scalarData);
            const dataBlob = new Blob([scalarData], { type: 'application/octet-stream' })
            zip.file('scalarData.bin', dataBlob);

            zip.generateAsync({type:'blob'}).then((content) => {
                saveAs(content, 'segmentation.zip')
                setIsProcessingLayer(false)
            })
        })
    }

    const importSegmentation = () => {
        console.log('SEGS AT IMPORT', segmentations);

        let viewport = viewports[activeViewportIndex];
        if (viewport.viewportOptions.viewportType !== 'volume') {
            viewport = viewports.find(vp => vp.viewportOptions.viewportType === 'volume');
            console.assert(viewport, 'ImportSegmentation: Could not find "volume" viewport');
            viewportGridService.setActiveViewportIndex(viewport.viewportIndex);
        }
        const cornerstoneVP = cornerstoneViewportService.getCornerstoneViewport(viewport.id);
        const actorEntries = cornerstoneVP.getActors();
        console.assert(actorEntries && actorEntries.length, 'ImportSegmentation: No actors on volume viewport');
        const imageVolumes = actorEntries.map((va) => {
            const actorUID = va.referenceId ?? va.uid;
            return cache.getVolume(actorUID);
        });
        const segmentationVolume = imageVolumes.filter(iv => !iv.imageIds)[0];
        const segmentationId = segmentationVolume.volumeId;
        const segmentationScalarData = segmentationVolume.getScalarData();

        // Load binary file
        const input = document.createElement('input');
        input.type = 'file';
        input.accept = 'application/octet-stream';
        input.onchange = async _ => {
            const file = Array.from(input.files)[0];
            const reader = new FileReader();
            reader.onload = async function(event) {
                const newScalarData = new Uint8Array(event.target.result as ArrayBuffer);
                // Replace values of current scalar data
                for (let i = 0; i < segmentationScalarData.length; i++) {
                    segmentationScalarData[i] = newScalarData[i];
                }
                // Trigger rendering
                cstSegmentation.triggerSegmentationEvents.triggerSegmentationDataModified(segmentationId);
            }
            reader.readAsArrayBuffer(file);
        };
        input.click();
    }

    return (
        <div className="flex flex-col min-h-0 font-inter font-[300]">
            <div className="bg-primary-dark">
                <div
                    className="flex cursor-pointer items-center"
                    onClick={evt => {
                        evt.stopPropagation();
                        setIsMinimized(!isMinimized);
                    }}
                >
                    <Icon
                        name="panel-group-open-close"
                        className={classNames(
                            'w-5 h-5 text-white transition duration-300 cursor-pointer',
                            {
                                'transform rotate-90': !isMinimized,
                            }
                        )}
                    />
                    <span className="text-[#d8d8d8] text-[12px] font-[300]">
                        {'Segmentations I/O'}
                    </span>
                </div>
                {/* active segmentation */}
                {!isMinimized && (
                    <div className="pl-2 pr-2 pb-[9px] pt-[9px]" style={{display:'flex', flexDirection:"column"}}>
                        <div className="text-[#d8d8d8] text-[12px] font-[300] justify-evenly flex">
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={importSegmentation}
                            >{'Import Segmentation'}</Button>
                            <Button
                                fullWidth={true}
                                size={'small'}
                                color={'secondary'}
                                onClick={() => {
                                    setIsProcessingLayer(true);
                                    exportSegmentation;
                                }}
                            >{'Export Segmentation'}</Button>
                        </div>
                        {isProcessingLayer &&
                            <div className={'w-full mt-[9px]'}>
                                <p style={{color:'white'}}>{'Processing layers...'}</p>
                                <ProgressBar
                                    className={'w-full mt-[3px]'}
                                    bgColor={processingLayerPercent < 100 ? '#5ACCE6' : '#50C878'}
                                    completed={processingLayerPercent}
                                    transitionDuration={"0.05s"}
                                    transitionTimingFunction={"linear"}
                                />
                            </div>
                        }
                    </div>
                )}
                <div className="h-[6px] bg-black "></div>
            </div>
        </div>
    )
}

SegmentationImportExport.propTypes = {
    segmentations: PropTypes.array.isRequired
}

export default SegmentationImportExport
